'use client';

import { useState, useRef, useEffect } from 'react';
import { Message } from '../data/chatData';
import MessageBubble from './MessageBubble';
import TypingIndicator from './TypingIndicator';
import SuggestedQuestions from './SuggestedQuestions';
import ContactHeader from './ContactHeader';

interface ChatAreaProps {
  messages: Message[];
  onSendMessage: (content: string) => void;
  isTyping: boolean;
  suggestedQuestions: string[];
  onToggleSidebar: () => void;
}

export default function ChatArea({
  messages,
  onSendMessage,
  isTyping,
  suggestedQuestions,
  onToggleSidebar
}: ChatAreaProps) {
  const [input, setInput] = useState('');
  const messagesEndRef = useRef<HTMLDivElement>(null);
  const textareaRef = useRef<HTMLTextAreaElement>(null);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages, isTyping]);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (input.trim() && !isTyping) {
      onSendMessage(input.trim());
      setInput('');
      if (textareaRef.current) {
        textareaRef.current.style.height = 'auto';
      }
    }
  };

  const handleKeyDown = (e: React.KeyboardEvent<HTMLTextAreaElement>) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSubmit(e);
    }
  };

  const handleInput = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    setInput(e.target.value);
    // Auto-resize textarea
    e.target.style.height = 'auto';
    e.target.style.height = Math.min(e.target.scrollHeight, 200) + 'px';
  };

  const handleSuggestedQuestion = (question: string) => {
    if (!isTyping) {
      onSendMessage(question);
    }
  };

  return (
    <div className="flex-1 flex flex-col bg-[#212121] overflow-hidden">
      {/* Contact Header */}
      <ContactHeader />

      {/* Mobile Menu Button */}
      <div className="lg:hidden border-b border-white/10 px-4 py-3">
        <button
          onClick={onToggleSidebar}
          className="text-white/70 hover:text-white"
        >
          <svg className="w-6 h-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
          </svg>
        </button>
      </div>

      {/* Messages Area */}
      <div className="flex-1 overflow-y-auto">
        <div className="max-w-3xl mx-auto px-4 py-6">
          {messages.length === 0 ? (
            <div className="flex flex-col items-center justify-center min-h-[50vh] text-center">
              <div className="w-16 h-16 rounded-xl bg-gradient-to-br from-purple-500 to-pink-500 flex items-center justify-center text-white font-bold text-2xl mb-6">
                S
              </div>
              <h1 className="text-3xl font-semibold text-white mb-3">
                Hi! I'm Swapnil
              </h1>
              <p className="text-white/60 text-lg mb-8 max-w-md">
                Ask me anything about my work, skills, or experience. Try one of these questions:
              </p>
              <SuggestedQuestions
                questions={suggestedQuestions}
                onQuestionClick={handleSuggestedQuestion}
              />
            </div>
          ) : (
            <>
              {messages.map((message) => (
                <MessageBubble key={message.id} message={message} />
              ))}
              {isTyping && <TypingIndicator />}
              <div ref={messagesEndRef} />
            </>
          )}
        </div>
      </div>

      {/* Input Area */}
      <div className="border-t border-white/10 bg-[#212121]">
        <div className="max-w-3xl mx-auto px-4 py-4">
          {messages.length > 0 && !isTyping && (
            <div className="mb-3">
              <SuggestedQuestions
                questions={suggestedQuestions.slice(0, 3)}
                onQuestionClick={handleSuggestedQuestion}
              />
            </div>
          )}
          <form onSubmit={handleSubmit}>
            <div className="relative bg-[#2f2f2f] rounded-2xl border border-white/10 focus-within:border-white/20 transition-colors">
              <textarea
                ref={textareaRef}
                value={input}
                onChange={handleInput}
                onKeyDown={handleKeyDown}
                placeholder="Send a message..."
                rows={1}
                disabled={isTyping}
                className="w-full bg-transparent text-white placeholder-white/40 px-4 py-3 pr-12 resize-none focus:outline-none max-h-[200px] overflow-y-auto"
                style={{ minHeight: '52px' }}
              />
              <button
                type="submit"
                disabled={!input.trim() || isTyping}
                className={`absolute right-2 bottom-2 w-8 h-8 rounded-lg flex items-center justify-center transition-colors
                  ${input.trim() && !isTyping
                    ? 'bg-white text-black hover:bg-white/90'
                    : 'bg-white/10 text-white/40 cursor-not-allowed'
                  }`}
              >
                <svg className="w-5 h-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 10l7-7m0 0l7 7m-7-7v18" />
                </svg>
              </button>
            </div>
          </form>
          <div className="text-center text-xs text-white/30 mt-3">
            This is a static demo portfolio. Responses are pre-defined.
          </div>
        </div>
      </div>
    </div>
  );
}
