'use client';

import { Message } from '../data/chatData';

interface MessageBubbleProps {
  message: Message;
  isStreaming?: boolean;
}

export default function MessageBubble({ message, isStreaming }: MessageBubbleProps) {
  const isUser = message.role === 'user';

  return (
    <div className={`flex gap-3 ${isUser ? 'justify-end' : 'items-start'} mb-6`}>
      {!isUser && (
        <div className="w-8 h-8 rounded-sm bg-gradient-to-br from-purple-500 to-pink-500 flex items-center justify-center text-white font-bold text-sm flex-shrink-0">
          S
        </div>
      )}

      <div className={`max-w-[85%] md:max-w-[70%]`}>
        <div
          className={`
            rounded-2xl px-4 py-3
            ${isUser
              ? 'bg-[#2f2f2f] text-white'
              : 'bg-transparent text-white/90'
            }
          `}
        >
          <div className="text-[15px] leading-relaxed whitespace-pre-wrap break-words">
            {message.content}
            {isStreaming && (
              <span className="inline-block w-1.5 h-4 bg-white/70 ml-0.5 animate-pulse"></span>
            )}
          </div>
        </div>
      </div>

      {isUser && (
        <div className="w-8 h-8 rounded-full bg-blue-600 flex items-center justify-center text-white font-bold text-sm flex-shrink-0">
          U
        </div>
      )}
    </div>
  );
}
